
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  min-width: 800px;
  color: #333;
  font-size: 15px;
  overflow: hidden;
  background-color: #f0f0f0;
}
#app .music-wrapper {
  height: 100%;
}
#app .music-wrapper .music-heading {
  position: fixed;
  width: 100%;
  min-width: 800px;
  height: 60px;
  z-index: 100;
}
#app .music-wrapper .music-content {
  position: relative;
  width: 800px;
  height: 100%;
  overflow: hidden;
  margin: 0 auto;
  padding-top: 60px;
}



